#waifu-toggle {
  background-color: #fa0;
  border-radius: 5px;
  bottom: 66px;
  color: #fff;
  cursor: pointer;
  font-size: 12px;
  left: 0;
  margin-left: -100px;
  padding: 5px 2px 5px 5px;
  position: fixed;
  transition: margin-left 1s;
  width: 80px;
  writing-mode: vertical-rl;
  z-index: 100;
}

#waifu-toggle.waifu-toggle-active {
  margin-left: -50px;
}

#waifu-toggle.waifu-toggle-active:hover {
  margin-left: -30px;
}

#waifu {
  bottom: -500px;
  left: 0;
  line-height: 0;
  position: fixed;
  transition: transform .3s ease-in-out, bottom 1s ease-in-out;
  z-index: 100;
  transform: translateY(6px);
  font-size: 1rem;
}

#waifu:hover {
  transform: translateY(6px);
}

#waifu-tips {
  animation: shake 50s ease-in-out 5s infinite;
  background-color: rgba(236, 217, 188, .5);
  border: 1px solid rgba(224, 186, 140, .62);
  border-radius: 12px;
  box-shadow: 0 3px 15px 2px rgba(191, 158, 118, .2);
  margin: -30px 20px;
  opacity: 0;
  overflow: hidden;
  padding: 5px 10px;
  position: absolute;
  text-overflow: ellipsis;
  transition: opacity 1s;
  word-break: break-all;
  pointer-events: none;
  margin-top: -5em;
  font-size: 0.8em;
  line-height: 1.5em;
  width: 12em;
  min-height: 3em;
}

#waifu-tips.waifu-tips-active {
  opacity: 1;
  transition: opacity .2s;
}

#waifu-tips span {
  color: #0099cc;
}

#live2d {
  cursor: grab;
  width: 9em;
  height: 9em;
}

#live2d:active {
  cursor: grabbing;
}

#waifu-tool {
  color: #aaa;
  opacity: 0;
  position: absolute;
  right: -10px;
  top: 1em;
  transition: opacity 1s;
}

#waifu:hover #waifu-tool {
  opacity: 1;
}

#waifu-tool span {
  color: #5b6c7d;
  cursor: pointer;
  display: block;
  font-size: 0.9em;
  line-height: 1.2em;
  text-align: center;
  transition: color .3s;
}

#waifu-tool span:hover {
  color: #0684bd; /* #34495e */
}

@media (max-width: 360px) {
  #waifu {
    display: none;
  }
}

@keyframes shake {
  2% {
    transform: translate(.5px, -1.5px) rotate(-.5deg);
  }

  4% {
    transform: translate(.5px, 1.5px) rotate(1.5deg);
  }

  6% {
    transform: translate(1.5px, 1.5px) rotate(1.5deg);
  }

  8% {
